import React, { useState, useEffect, useMemo } from "react";

import axios from "axios";
import Modal from "react-bootstrap/Modal";
import Spinner from "react-bootstrap/Spinner";
import Button from "react-bootstrap/Button";
import { backendUrl } from "../utils/fetchUtils";
import ResultTable from "../common/table/ResultTable";
import { NrBadge } from "../common/NrBadge";
import { CarDiv, TeamDiv } from "../common/Div";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faQuestion } from "@fortawesome/free-solid-svg-icons";

export const TeamListModal = ({ show, handleClose, eventId, started }) => {
  const [teams, setTeams] = useState([]);
  const [startEvent, setStartEvent] = useState(false);
  const [loading, setLoading] = useState(true);
  const [referee, setReferee] = useState(false);

  const fetchTeams = () => {
    if (eventId === undefined) return;
    axios
      .get(`${backendUrl()}/event/getTeams?eventId=${eventId}`)
      .then((res) => {
        setTeams(res.data);
        setLoading(false);
      });
  };

  useEffect(() => {
    if (show) {
      setLoading(true);
      setTeams([]);
    }
    fetchTeams();
  }, [show]);

  useEffect(() => {
    setTeams([]);
    fetchTeams();
  }, [referee]);

  const columns = useMemo(
    () => [
      {
        width: "8%",
        id: "index",
        Header: "L.p",
        accessor: (cellInfo) => cellInfo.number,
        disableFilters: true,
        Cell: (row) => <div className="ps-2"> {row.row.index + 1}</div>,
      },
      {
        width: "9%",
        id: "#",
        Header: "#Nr",
        accessor: (cellInfo) => cellInfo.number,
        disableFilters: true,
        Cell: (row) => <NrBadge value={row.value}></NrBadge>,
      },
      {
        width: "30%",
        id: "team",
        Header: "Zaoga",
        accessor: (cellInfo) => cellInfo.team,
        disableFilters: true,
        Cell: (row) => <TeamDiv team={row.value}></TeamDiv>,
      },
      {
        width: "30%",
        id: "car",
        Header: "Samochd",
        accessor: (cellInfo) => cellInfo.team.currentCar,
        disableFilters: true,
        Cell: (row) => (
          <CarDiv
            line1={(row.value?.brand || "") + " " + (row.value?.model || "")}
            line2={row.row.original.carClass.name}
            carBrand={row.value?.brand}
            driveType={row.value?.driveTypeEnum}
          ></CarDiv>
        ),
      },
      {
        width: "11%",
        id: "engine",
        Header: "Silnik",
        accessor: (cellInfo) =>
          cellInfo.team.currentCar?.engineCapacity + " cm3",
        disableFilters: true,
      },
      {
        width: "11%",
        id: "entryFee",
        Header: "Potwierdz.",
        accessor: (cellInfo) => cellInfo.entryFeePaid,
        disableFilters: true,
        Cell: (row) => {
          return (
            <FontAwesomeIcon
              icon={row.value ? faCheck : faQuestion}
              style={{ color: row.value ? "green" : "black" }}
              title={
                row.value ? "Potwierdzony" : "Dodaj potwierdzenie przelewu"
              }
              cursor={"pointer"}
            />
          );
        },
      },
    ],
    []
  );
  return (
    <Modal
      show={show}
      onHide={handleClose}
      backdrop="static"
      keyboard={false}
      size="xl"
    >
      <Modal.Header className="bg-dark-green text-white" closeButton>
        <Modal.Title>Lista zapisanych</Modal.Title>
      </Modal.Header>
      <Modal.Body className="px-0">
        {loading && (
          <div className="text-center">
            <Spinner animation="border" variant="secondary" size="lg" />
          </div>
        )}
        {!loading && teams.length === 0 && (
          <h2 className="text-center">Brak zgosze - bd pierwszy!</h2>
        )}
        {!loading && teams?.length > 0 && (
          <ResultTable
            columns={columns}
            data={teams}
            isLoading={false}
            isFooter={false}
            isHeader={true}
            manualPagination={true}
          />
        )}
      </Modal.Body>
      <Modal.Footer className={"justify-content-center"}>
        <div className="d-grid">
          {/* {referee && (
            <Button
              className={"m-2"}
              variant="success"
              onClick={() => setStartEvent(true)}
              disabled={started}
            >
              Zamknij liste / Rozpocznij wydarzenie
            </Button>
          )} */}
          <Button className={"m-1"} variant="secondary" onClick={handleClose}>
            Anuluj
          </Button>
        </div>
      </Modal.Footer>
    </Modal>
  );
};
